export default {
	name:"tuiBottomPopup",
	props: {
		//是否需要mask
		mask: {
			type: Boolean,
			default: true
		},
		//控制显示
		show: {
			type: Boolean,
			default: false
		},
		//背景颜色
		bgcolor: {
			type: String,
			default: "#fff"
		},
		//高度 upx
		height: {
			type: Number,
			default: 0
		}
	},
	methods: {
		handleClose() {
			if (!this.show) {
				return;
			}
			this.$emit('close', {});
		},
		px(num){
			return uni.upx2px(num) + 'px'
		}
	}
}
